-----END RSA PRIVATE KEY-----`;

function getSign() {
  let str = `{"userId":388683, "timestamp": ${+new Date()}}`;
  let rsa = new RSA.RSAKey();
  rsa.readPrivateKeyFromPEMString(signPrivateKey);
  let hSig = rsa.privEncrypt(str);
  return RSA.hex2b64(hSig);
}

export default {
  name: "App",
  data() {
    return {
      map: null,
      heatmap: null,
      heatmapData: [],
      city_id: 62,
      form: {
        city_id: 62,
        end_period: null,
        start_period: null,
        times: null,
        type_service: null,
        businesstype_id: null,
        classification: null,
      },
      nowCity: null,
      maxNum: 30,
      mapFlag: false,
      business_id: null,
      business: [],
      businessType: {},
      businessTypeList: [],
      businessList: [],
      togFlag: false,
    };
  },
  methods: {
    //获取定位
    getLocation() {
      let _that = this;
      let geolocation = location.initMap("map-container"); //定位
      AMap.event.addListener(geolocation, "complete", (result) => {
        _that.lat = result.position.lat;
        _that.lng = result.position.lng;
        _that.province = result.addressComponent.province;
        _that.city = result.addressComponent.city;
        _that.district = result.addressComponent.district;
        console.log("result :>>", JSON.parse(JSON.stringify(result)));
      });
    },
    menuTogg() {
      this.togFlag = !this.togFlag;
    },
    resetForm(formName) {
      // console.log(this.$refs[formName]);
      if (this.$refs[formName] !== undefined) {
        this.$refs[formName].resetFields();
      }
    },
    submit() {
      if (this.mapFlag) {
        return;
      }
      this.mapFlag = true;
      setTimeout(() => {
        this.mapFlag = false;
      }, 2000);

      this.$axios({
        url: "https://php.51jjcx.com/backstage/Order/ThermodynamicChart",
        method: "post",
        data: {
          sign: getSign(),
          city_id: 62,
          times: this.form.times,
          end_period: this.form.end_period,
          start_period: this.form.start_period,
        },
      }).then((res) => {
        this.heatmapData = res.data.data;
        setTimeout(() => {
          this.initMap();
          this.createHeatMap();
        }, 800);

        console.log("res :>>", JSON.parse(JSON.stringify(res)));
      });
      this.togFlag = false;
    },
    initMap() {
      this.map = new AMap.Map("container", {
        resizeEnable: true,
        center: [126.661892,45.742371],

        zoom: 10,
        mapStyle: "amap://styles/normal", // 极夜蓝
        //自定义地图样式：https://lbs.amap.com/dev/mapstyle/index
      });
    },
    //判断浏览区是否支持canvas
    isSupportCanvas() {
      let elem = document.createElement("canvas");
      return !!(elem.getContext && elem.getContext("2d"));
    },
    createHeatMap() {
      if (!this.isSupportCanvas()) {
        return this.$msg.error(
            "热力图仅对支持canvas的浏览器适用,您所使用的浏览器不能使用热力图功能,请换个浏览器试试。"
        );
      }
      let __this = this;
      this.map.plugin(["AMap.Heatmap"], function () {
        //初始化heatmap对象
        __this.heatmap = new AMap.Heatmap(__this.map, {
          visible: false,
          radius: 35, //给定半径
          opacity: [0, 0.8],
        });

        __this.heatmap.setDataSet({
          data: __this.heatmapData,
          max: __this.maxNum,
        });
      });
    },
  },
  mounted() {
    // 延迟加载，防止出现AMap not defined
    // this.submit();
    setTimeout(() => {
      console.log("this.$route :>>", this.$route.query);
    }, 1000);
    this.getLocation();
  },
  created() {
    this.submit();
  },
  beforeDestroy() {
    this.map && this.map.destroy();
  },
};
</script>

