import Jsrsasign from 'jsrsasign'

Jsrsasign.RSAKey.prototype.privEncrypt = function(d) {
    var a = pkcs1pad1(d, (this.n.bitLength() + 7) >> 3);
    if (a == null) {
        return null;
    }
    var e = this.doPrivate(a);
    if (e == null) {
        return null;
    }
    //转成16进制的字符串
    var b = e.toString(16);
    if ((b.length & 1) == 0) {
        return b;
    } else {
        return "0" + b;
    }
};

function pkcs1pad1(e, h) {
    if (h < e.length + 11) {
        throw "Message too long for RSA";
        return null;
    }
    var g = new Array();
    var d = e.length - 1;
    while (d >= 0 && h > 0) {
        var f = e.charCodeAt(d--);
        g[--h] = f;
    }
    g[--h] = 0;
    while (h > 2) {
        g[--h] = 255;
    }
    g[--h] = 1;
    g[--h] = 0;
    return new Jsrsasign.BigInteger(g);
}
export default Jsrsasign